import _ from 'lodash';
// import Row from './Row';
// import Cell from './Cell';

class Game extends React.Component {
  constructor(props) {
    super(props);
    this.matrix = [];
    const { width, height } = props;
    for (let h = 0; h < height; h++) {
      let row = [];
      for (let w = 0; w < width; w++) {
        row.push(`${h}-${w}`);
      }
      this.matrix.push(row);
    }
    const flatMatrix = _.flatten(this.matrix);
    const totalEnemies = Math.ceil(((width * height) / (width + height))* 2);
    this.activeCells = _.sampleSize(flatMatrix, totalEnemies);
    this.inactiveCells = flatMatrix.filter((eachCell) => this.activeCells.indexOf(eachCell) === -1);
    this.state = {
      gameStatus: 'ready',
      totalSteps: 0,
      gameScore: 0,
      totalEnemies,
    }
  }
  componentWillUnmount() {
    this.finishGame();
  }
  finishGame = (gameState) => {
    // the game is over
    // so set score and cleanup
  }
  render() {
    const { gameState } = this.state;
    const {  } = this.props;
    return (
      <div className="grid">
        This is the best game
      </div>
    );
  }
}

export default Game;



